                    <ul class="option-list clearfix hidden">
                        <li date-value="2" data-txt="黄浦区">黄浦区</li>
                        <li date-value="3" data-txt="虹口区">虹口区</li>
                        <li date-value="2" data-txt="黄浦区">黄浦区</li>
                        <li date-value="3" data-txt="虹口区">虹口区</li>
                        <li date-value="2" data-txt="黄浦区">黄浦区</li>
                        <li date-value="3" data-txt="虹口区">虹口区</li>
                        <li date-value="2" data-txt="黄浦区">黄浦区</li>
                        <li date-value="3" data-txt="虹口区">虹口区</li>
                        <li date-value="2" data-txt="黄浦区">黄浦区</li>
                        <li date-value="3" data-txt="虹口区">虹口区</li>
                    </ul>
                    <ul class="option-list clearfix hidden">
                        <li date-value="2" data-txt="白鹤镇">白鹤镇</li>
                        <li date-value="3" data-txt="康桥镇">康桥镇</li>
                        <li date-value="2" data-txt="白鹤镇">白鹤镇</li>
                        <li date-value="3" data-txt="康桥镇">康桥镇</li>
                        <li date-value="2" data-txt="白鹤镇">白鹤镇</li>
                        <li date-value="3" data-txt="康桥镇">康桥镇</li>
                    </ul>
                </div>
            </div>
        -->
      </div>
      <div class="edit-footer">
        <a href="javascript:;" class="btn btn-primary confirm" @click="submit_address">保存</a>
        <a href="javascript:;" class="btn btn-gray close" @click="isShow_editAddress=false">取消</a>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../utils/api";
import { mapState, mapGetters } from "vuex";

export default {
  created() {
    document.title = "确认订单";
  },
  //   元素挂载之后请求购物车数据
  // async函数
  async mounted() {
    
    await this.$store.dispatch("address/CHANGEaddressList");
    await this.$store.dispatch("cart/CHANGEcartGoodsList");
    this.orderGoods = this.orderGoods_id.map((item, index, arr) => {
      return this.findGoodsInCartGoodsList(item);
    });
  },

  data() {
    return {
      orderGoods: [],
      orderGoods_id: JSON.parse(this.$route.query.id),
      //   isShow_editAddress是否显示编辑地址窗口
      isShow_editAddress: false,
      // Selected_adress选择了第几个地址项
      Selected_adress: 0,
      // address_form地址表单
      address_form: {}
    };
  },
  computed: {
    ...mapState("cart", ["cartGoodsList"]),
    ...mapState("address", ["addressList"]),
    addressList_reverser() {
      return [...this.addressList].reverse();
    },
    totalOrderGoodsAmount() {
      return this.orderGoods.reduce((sum, item) => {
        return (sum += Number(item.quantity));
      }, 0);
    },
    totalOrderGoodsPrice() {
      return this.orderGoods.reduce((sum, item) => {
        return (sum += Number(item.quantity) * Number(item.price));
      }, 0);
    }
  },

  methods: {
    // findGoodsInCartGoodsList用于根据id在购物车数组中查找商品
    findGoodsInCartGoodsList(id) {
      for (var i = 0; i < this.cartGoodsList.length; i++) {
        if (this.cartGoodsList[i].id == id) {
          return this.cartGoodsList[i];
        }
      }
    },
    // add_Address点击“添加新地址”
    add_Address() {
      this.isShow_editAddress = true;
      this.address_form = {};
    },
    // modifyAddress点击地址修改
    modifyAddress() {
      this.isShow_editAddress = true;
      this.address_form = this.addressList_reverser[this.Selected_adress];
      // console.log(this.addressList);
    },
    // submit_address点击地址保存
    submit_address() {
      this.$store
        .dispatch("address/CHANGEaddressList_update", this.address_form)
        .then(data => {
          // console.log(this.addressList_reverser);
        });
      this.isShow_editAddress = false;
    },
    // checkout_pay点击结算按钮
    checkout_pay() {
      api
        .post("/user/checkout", {
          ids: this.orderGoods_id.toString(","),
          address_id: this.addressList_reverser[this.Selected_adress].address_id
        })
        .then(data => {
          this.$router.push({
            name: "confirm",
            query: {
              order_no: data,
              address: JSON.stringify(this.addressList_reverser[this.Selected_adress]),
              orderGoods: JSON.stringify(this.orderGoods),
              totalOrderGoodsPrice:this.totalOrderGoodsPrice,
            }
          });
        });
    },
    fa() {}
  }
};
</script>

<style lang="scss" scoped>
@import url(../css/iconfont.css);
@import url(../css/style.css);
@import url(../css/checkout.css);
</style>